import { usersAPI } from "../api/api";
import { profileAPI } from "../api/api";

const ADD_POST='ADD-POST';
// const UPDATE_NEW_POST_TEXT='UPDATE-NEW-POST-TEXT';
const SET_USER_PROFILE='SET-USER-PROFILE';
const SET_STATUS='SET-STATUS';

let initialState={
  posts:[
      {id: 1,message:'Hi,how are you?', countlike:0},
      {id: 2,message:'It is my first post', countlike:2},
      {id: 3,message:'blabla', countlike:4},
      {id: 4,message:'blablabla', countlike:8}],

    profile:null,
    status:"",
  };

export const profileReducer=(state=initialState,action)=> {
  switch(action.type) {
    case ADD_POST:{
      let newPost={
          id:5,
          message:action.newPostText,
          countlike:0,
        };
        return {
          ...state,
          posts:[...state.posts,newPost],
          newPostText:''
        };
      }
   
          case SET_USER_PROFILE:{
            return {...state,profile:action.profile };
        }   
         
        case SET_STATUS:{
          return {...state,status:action.status };
      }   
              
          default:
         return state;   
    
    }   
 
}
   
export const addPostActionCreator=(newPostText)=>({type:ADD_POST,newPostText});
export const setUserProfile=(profile)=>({type:SET_USER_PROFILE,profile});
export const setStatus=(status)=>({type:SET_STATUS,status});

export const getUserId=(userId)=>async(dispatch)=>{
  let response=await usersAPI.getUserId(userId)
  dispatch(setUserProfile(response.data)); 
    
    
}

export const getStatus=(userId)=> async(dispatch)=>{
  let response=await profileAPI.getStatus(userId)
  dispatch(setStatus(response.data)); 
}    
    
   

export const updateStatus=(status)=> async(dispatch)=>{
  let response=await profileAPI.updateStatus(status)
  if(response.data.resultCode===0) {
    dispatch(setStatus(status)); 
    }
      
}
export default profileReducer;
