
import styles from './styles.module.css';
import userPhoto from '../../components/assets/img/images.png';
import {NavLink} from 'react-router-dom';

import Paginator from './paginator';

let Users=(props)=>{
    return <div>
       <Paginator currentPage={props.currentPage} onPageChanged={props.onPageChanged}
       totalItemsCount={props.totalUsersCount}  pageSize={props.pageSize}/>
    
       {
          props.users.map(u=><div key={u.id}>
              
           <span>
              
           <div>
              <NavLink to={'/profile/' + u.id}>
            <img src={ u.photos.small !=null ? u.photos.small : userPhoto} className={styles.photo} src={u.photoURL}/>
            </NavLink>
           </div>
            <div>
         
            {u.followed 
      
            ? <button disabled={props.followingInProgress} onClick={() => {props.unfollow(u.id)}}>Unfollow</button>
            : <button disabled={props.followingInProgress} onClick={() => {props.follow(u.id)}}>Follow</button>}
            </div>
           </span>
           <span>
              <div>{u.name}</div><div>{u.status}</div>
           </span>
           <span>
               <div>{"u.location.city"}</div>
               <div>{"u.location.country"}</div>
              </span>
           </div>)
       }
      
      </div>
}
export default Users;
           
           
           
         
             
           
               
               
            
          