
import {connect} from 'react-redux';

import {follow,unfollow,setCurrentPage,toggleFollowingProgress,requestUsers} from '../../redux/users-reducer';
import React from  'react';
import Users from './Users';

import Preloader from '../Preloader/Preloader';

import { compose } from 'redux';
import {getCurrentPage,getFollowingProgress,getUsers,getIsFetching,getPageSize,getTotalUsersCount} from '../../redux/users-selectors';

class UsersContainer extends React.Component {
  
    componentDidMount() {
       this.props.requestUsers(this.props.page,this.props.pageSize);
  
       
   }
   onPageChanged=(pageNumber)=>{
      this.props.requestUsers(pageNumber,this.props.pageSize);      

   }
   render() {
   
    
      return <> 
       {this.props.isFetching ? <Preloader/> :null}
      <Users totalUsersCount={this.props.totalUsersCount}
                            pageSize={this.props.pageSize}
                            currentPage={this.props.currentPage}
                            users={this.props.users}
                            onPageChanged={this.onPageChanged}
                           follow={this.props.follow}
                           unfollow={this.props.unfollow}
                        
                           followingInProgress={this.props.followingInProgress}
                         
      />
      </>
   }
} 



let mapStateToProps=(state)=>{
   console.log('mapStateToProps')
   return{
       //users:getUsers(state),
       users:getUsers(state),
       pageSize:getPageSize(state),
       totalUsersCount:getTotalUsersCount(state),
       currentPage:getCurrentPage(state),
       isFetching:getIsFetching(state),
       toggleFollowingProgress:getFollowingProgress(state),

 }
}


export default compose(
   connect(mapStateToProps,{ follow,unfollow,setCurrentPage,toggleFollowingProgress,requestUsers})
)(UsersContainer)
      
      

    
       
        
        
          
        
        
            

        
        
        
            
        
          
        
    